<template>
    <button class="btn btn-sm btn-outline-dark" data-toggle="modal" data-target="#menu-game-modal" ref="btn_menu" @click="open_modal()">
        <i class="bi bi-gear"></i>
        Paramètre
    </button>

    <!-- Modal -->
    <div class="modal fade" id="menu-game-modal" tabindex="-1" role="dialog" aria-labelledby="menu-game-modal-title" aria-hidden="true">
        <div class="modal-dialog modal-dialog-centered" role="document">
            <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title">Paramètre</h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <div class="d-flex flex-column align-items-center">
                        <p class="m-0 mb-2 text-center">Vitesse</p>
                        <div class="btn-group" role="group">
                            <button type="button" class="btn" :class="{'btn-dark': move_speed=='slow', 'btn-outline-dark': move_speed!='slow'}" @click="click_speed('slow')">Lente</button>
                            <button type="button" class="btn" :class="{'btn-dark': move_speed=='normal', 'btn-outline-dark': move_speed!='normal'}" @click="click_speed('normal')">Moyenne</button>
                            <button type="button" class="btn" :class="{'btn-dark': move_speed=='fast', 'btn-outline-dark': move_speed!='fast'}" @click="click_speed('fast')">Rapide</button>
                        </div>
                    </div>

                    <div class="">
                        <div class="mt-5 d-flex justify-content-center">
                            <button v-if="!wanna_retry" class="btn btn-block btn-outline-dark" @click="try_retry()">Recommencer</button>
                            <div v-else class="">
                                <p class="text-center m-1">Revoir les configurations et recommencer ?</p>
                                <div class="d-flex justify-content-around">
                                    <button class="btn btn-sm btn-outline-dark m-2" @click="retry(false)">Non</button>
                                    <button class="btn btn-sm btn-outline-dark m-2" @click="retry(true)">Oui</button>
                                </div>
                            </div>
                        </div>
                        <div class="mt-3 d-flex justify-content-center">
                            <button v-if="!wanna_quit" class="btn btn-block btn-outline-dark" @click="try_quit()">Accueil</button>
                            <div v-else class="">
                                <p class="text-center m-1">Quitter et aller à l'accueil ?</p>
                                <div class="d-flex justify-content-around">
                                    <button class="btn btn-sm btn-outline-dark m-2" @click="quit(false)">Non</button>
                                    <button class="btn btn-sm btn-outline-dark m-2" @click="quit(true)">Oui</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

export default {
    components: {

    },
    props: {
        speed: {
            type: String,
            default: "normal"
        }
    },
    data () {
        return {
            move_speed: this.speed,
            wanna_retry: false,
            wanna_quit: false
        }
    },
    methods: {
        click_speed(speed) {
            this.move_speed = speed
            let speed_update = 400
            if (speed == 'normal') {
                speed_update = 400
            }
            else if (speed == 'slow') {
                speed_update = 1200
            }
            else if (speed == 'fast') {
                speed_update = 90
            }

            this.$emit('speed', speed_update)
        },
        try_retry() {
            this.wanna_retry = true
        },
        retry(is_retry) {
            this.wanna_retry = is_retry
            if (is_retry) {
                this.trigger_close_modal()
                this.$emit("retry", true)
            }
        },
        open_modal() {
            this.wanna_retry = false
            this.wanna_quit = false
        },
        trigger_close_modal() {
            if (document.querySelectorAll('.modal.show').length > 0) {
                this.$refs.btn_menu.click()
            }
        },
        try_quit() {
            this.wanna_quit = true
        },
        quit(is_quit) {
            this.wanna_quit = is_quit
            if (is_quit) {
                this.trigger_close_modal()
                this.$router.push({path: '/'})
            }
        },
    },
    beforeUnmount() {
        this.trigger_close_modal()
    }
}
</script>

